<template>
<v-app>
  <v-toolbar color="primary" dark app fixed>
    <v-toolbar-title>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <span>Demo/Tutorial</span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items>

      <v-text-field
        flat
        solo-inverted
        prepend-icon="search"
        append-icon="search" @click:append="getCards"
        label="Search"
        class="hidden-sm-and-down"
        v-model="textSearch"
      ></v-text-field>

    </v-toolbar-items>
  </v-toolbar>
  <v-navigation-drawer fixed app v-model="drawer">
    <v-list dense v-if="this.$route.path !== '/'" >
      <template v-for="menu in menus">
        <v-list-tile :key="menu.text" :to="{name: menu.component}" >
          <v-list-tile-action>
            <v-icon>{{ menu.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              {{ menu.text }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>
    </v-list>
  </v-navigation-drawer>
  <v-content>
        <v-container
          fluid
          grid-list-md
        >
          <v-layout
            row
            wrap
          >
            <v-flex
              v-for="card in cards"
              :key="card.no_id"
              xs12
              sm6
            >
              <v-card>
                <v-img
                  :src="card.gambar"
                  height="200px"
                  class="white--text"
                >
                  <v-container
                    fill-height
                    fluid
                  >
                    <v-layout fill-height>
                      <v-flex
                        xs12
                        align-end
                        flexbox
                      >
                        <span
                          class="headline"
                          v-text="card.judul"
                        ></span>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-img>
                <v-card-title>
                    <div>
                        <span
                          v-text="card.artikel"
                        ></span>
                    </div>
                </v-card-title>                
                <v-card-actions>
                  <v-spacer></v-spacer>
                    <v-btn
                        flat
                        color="orange"
                    >
                        Share
                    </v-btn>
                    <v-btn
                        flat
                        color="orange"
                    >
                        Explore
                    </v-btn>
                </v-card-actions>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>  
  </v-content>
</v-app>
</template>

<script>
import CryptoJS from 'crypto-js/crypto-js'
var cipher_key = "U2FsdGVkX19LIRVpXKgXktK+An43KRL7LkKFfMtiVkY="
var cipher_url = "U2FsdGVkX19Ldmu7/QBsTsMSRu0nubape/9VAQUWzXcc5G0aUb4ytrWuGTvILnQWObuqApvvJ02rDZnUvOCV24TZZgkPJTaDSyZSd2FQ4AmutM22tD+uvvp2TM/4cgrMa5okSmAVSrSEnDBSVYUTCQ=="
var secret = CryptoJS.AES.decrypt(cipher_key, '123456').toString(CryptoJS.enc.Utf8);
var url = CryptoJS.AES.decrypt(cipher_url, secret).toString(CryptoJS.enc.Utf8);

//var url = "https://script.google.com/macros/s/AKfycbzigw2eOmKsLYSBzx1BoARoi-rxlp9RmZ0OAt6RGY9soqa_ieM/exec"

export default {
  data () {
    return {
        drawer: null,   
        menus: [
            { icon: 'home', text: 'Home', component: 'home' },
            { icon: 'home', text: 'Posts', component: 'posts' },
            { icon: 'home', text: 'Login', component: 'login' }            
          ],
        cards: [],
        textSearch: ""
      //
    }
  },
  computed: {
     cardsFilter: function() {
       var textSearch = this.textSearch;
       return this.cards.filter(function(el) {
         return el.name.toLowerCase().indexOf(textSearch.toLowerCase()) !== -1;
       });
     }
  }, 
  created: function(){
//    setInterval(() => {    
        this.getCards()
//    }, 1000)
  },
  methods: {
    getCards() {
      this.$http.get(url+"?sheet=Posts&action=retrieve&q="+this.textSearch)
        .then((response) => {
          this.cards = response.data;
        })
       .catch((error) => {
          this.cards = error.response.statusText;
        });
    }    
  }   
}
</script>
