<template>
<v-app>
  <v-toolbar color="primary" dark app fixed>
    <v-toolbar-title>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <span>Demo/Tutorial</span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items>
      <v-menu bottom left>
        <v-btn slot="activator" dark icon v-on:click="logout()">
          <span>Keluar</span>        
          <v-icon>exit_to_app</v-icon>
        </v-btn>
      </v-menu>
    </v-toolbar-items>
  </v-toolbar>
  <v-navigation-drawer fixed app v-model="drawer">
    <v-list dense v-if="this.$route.path !== '/'">
      <template v-for="menu in menus">
        <v-list-tile :key="menu.text" :to="{name: menu.component}" >
          <v-list-tile-action>
            <v-icon>{{ menu.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              {{ menu.text }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>
    </v-list>
  </v-navigation-drawer>
  <v-content>
  <v-container>
    <v-layout
      wrap
    >
      <v-flex xs12>
    <v-form ref="form" name="submit-to-google-sheet" @submit.prevent="processForm" >
        <v-text-field type="text" name="no_id" label="No ID" outline ></v-text-field>
        <v-text-field type="text" name="gambar" label="https://drive.google.com/uc?export=view&id=fileID" outline ></v-text-field>
        <v-text-field type="text" name="judul" label="Judul" outline ></v-text-field>
        <v-textarea name="artikel" label="Isi artikel" outline ></v-textarea>
        <v-text-field type="text" v-model="input.honeypot" label=""  class="honeypot"></v-text-field>         
        <v-btn type="submit">Submit</v-btn>
        <v-btn type="reset" >Reset</v-btn>
    </v-form>
      </v-flex>
    </v-layout>
  </v-container>    
  </v-content>
</v-app>
</template>

<script>
import CryptoJS from 'crypto-js/crypto-js'
var cipher_key = "U2FsdGVkX19LIRVpXKgXktK+An43KRL7LkKFfMtiVkY="
var cipher_url = "U2FsdGVkX199bUGWK1XlVn/ho6X0fZ/gXF+E8XjB0LwS9CAtbdTHaIGOIN/6WfD9EA1JopbdevrcUhCyEbsQYfCHuDdVVRDS5sjth6bv64VDrcNfycft9BtSTnK5WOoIxzDc3myvmfMLoY8ACJ80gA=="
var secret = CryptoJS.AES.decrypt(cipher_key, '123456').toString(CryptoJS.enc.Utf8);
var url = CryptoJS.AES.decrypt(cipher_url, secret).toString(CryptoJS.enc.Utf8);

//var url = "https://script.google.com/macros/s/AKfycbzigw2eOmKsLYSBzx1BoARoi-rxlp9RmZ0OAt6RGY9soqa_ieM/exec"

export default {
  data () {
    return {
        drawer: null,   
        menus: [
            { icon: 'home', text: 'Blog Post', component: 'blogpost' },
            { icon: 'home', text: 'Delete Post', component: 'deletepost' }
        ],
        dialog: false,
        input: {
            honeypot: ""
        }       
      //
    }
  },
  created: function(){

  },
  methods: {
    processForm: function () {
        if(this.input.honeypot == ""){
            var form = document.forms['submit-to-google-sheet']
            var formData = new FormData(form);    
            this.$http.post(url+"?sheet=Posts&action=create", formData).then(function(response){
                alert('Success!', response)
            })
        } 
    },
    logout: function () {
        this.$router.replace({ name: "home" });
    }        
  }   
}
</script>