<template>
<v-app>
  <v-toolbar color="primary" dark app fixed>
    <v-toolbar-title>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <span>Demo/Tutorial</span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items>
      <v-menu bottom left>
        <v-btn slot="activator" dark icon v-on:click="logout()">
          <span>Keluar</span>        
          <v-icon>exit_to_app</v-icon>
        </v-btn>
      </v-menu>
    </v-toolbar-items>
  </v-toolbar>
  <v-navigation-drawer fixed app v-model="drawer">
    <v-list dense v-if="this.$route.path !== '/'">
      <template v-for="menu in menus">
        <v-list-tile :key="menu.text" :to="{name: menu.component}" >
          <v-list-tile-action>
            <v-icon>{{ menu.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              {{ menu.text }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>
    </v-list>
  </v-navigation-drawer>
  <v-content>
  <v-container>
    <v-layout
      wrap
    >
      <v-flex xs12>

    <v-dialog
      v-model="dialog"
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">Delete Confirmation</v-card-title>
        <v-card-text :v-bind="identify">
          Selected ID : {{ identify }}
          Are you sure want to delete?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="secondary"
            text
            @click.prevent="processDelete"
          >
            Confirm
          </v-btn>          
          <v-btn
            color="green darken-1"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

      <v-card>
    <v-form ref="form" name="submit-to-google-sheet" >
        <v-text-field type="text" v-model="input.identify" label="Input text to delete" outline ></v-text-field>
        <v-text-field type="text" v-model="input.honeypot" label=""  class="honeypot"></v-text-field>  
        <v-btn type="submit" @click.prevent="processIdentify">Delete</v-btn>
        <v-btn type="reset" >Reset</v-btn>               
    </v-form>
        <v-card-actions>
    <v-spacer></v-spacer> 

        </v-card-actions>
      </v-card>
         
      </v-flex>
    </v-layout>
  </v-container>    
  </v-content>
</v-app>
</template>

<script>
import CryptoJS from 'crypto-js/crypto-js'
var cipher_key = "U2FsdGVkX19LIRVpXKgXktK+An43KRL7LkKFfMtiVkY="
var cipher_url = "U2FsdGVkX1/S1VMJz+UebFmWm7jjXCHIdeDzmc98btIh6zcUAl/zcBurmIfaE/DcblOC6nX+TnXbRTpg2biJDkIF0bAT+52OCopVTx2c3EQ6Gm/jb17aShYZgUY7LIo0ibh7cMS3bJFc1l3I6kFMig=="
var secret = CryptoJS.AES.decrypt(cipher_key, '123456').toString(CryptoJS.enc.Utf8);
var url = CryptoJS.AES.decrypt(cipher_url, secret).toString(CryptoJS.enc.Utf8);

//var url = "https://script.google.com/macros/s/AKfycbzigw2eOmKsLYSBzx1BoARoi-rxlp9RmZ0OAt6RGY9soqa_ieM/exec"

export default {
  data () {
    return {
        drawer: null,   
        menus: [
            { icon: 'home', text: 'Blog Post', component: 'blogpost' },
            { icon: 'home', text: 'Delete Post', component: 'deletepost' }
        ],
        dialog: false,
        identify: [],
        input: {
            identify: "",
            delete: "",
            honeypot: ""
        }       
      //
    }
  },
  created: function(){
//    this.processIdentify()
  },
  methods: {
    processIdentify: function () {
      this.$http.get(url+"?sheet=Posts&action=identify&cell="+this.input.identify)
        .then((response) => {
          this.identify = response.data;
          this.dialog = true;
        })
       .catch((error) => {
          this.identify = error.response.statusText;
        });
    },           
    processDelete: function () {
        if(this.input.honeypot == ""){
            this.$http.post(url+"?sheet=Posts&action=delete&rowId="+this.identify).then(function(response){           
                alert('Success!', response)
            })
        } 
    },
    logout: function () {
        this.$router.replace({ name: "home" });
    }        
  }   
}
</script>