<template>
    <v-container fluid fill-height>
        <v-layout align-center justify-center>
         <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Demo/Tutorial</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
                      <v-card-text>
                <v-form>
        <v-text-field outline prepend-icon="person" type="text" name="username" v-model="input.username" label="Nama pengguna" id="user" />
        <v-text-field outline prepend-icon="lock" :append-icon="hidePassword ? 'visibility' : 'visibility_off'" @click:append="togglePass" :type="passwordType" name="password" v-model="input.password" label="Password"  id="pass" />
        <v-text-field prepend-icon="" type="text" name="honeypot" v-model="input.honeypot" label=""  class="honeypot" />
                        </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
        <v-btn color="primary" type="button" v-on:click="login()">Masuk</v-btn>
                      </v-card-actions>
            </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import CryptoJS from 'crypto-js/crypto-js'
var cipher_key = "U2FsdGVkX19LIRVpXKgXktK+An43KRL7LkKFfMtiVkY="
var cipher_url = "U2FsdGVkX18H7Ga2kTQrY87cSsHWRC1dY15EU2Jk44fF0PNSxeXBXLdo6282ZZGR3czn8wrjFrOVPtmKs4JC3WWKoQ1jfyBcdC1+PdTpbMMagwKP151rbl7F794WA0FsiFIWagueJmqArreuj1vzGQ=="
var secret = CryptoJS.AES.decrypt(cipher_key, '123456').toString(CryptoJS.enc.Utf8);
var url = CryptoJS.AES.decrypt(cipher_url, secret).toString(CryptoJS.enc.Utf8);

//var url = "https://script.google.com/macros/s/AKfycbzigw2eOmKsLYSBzx1BoARoi-rxlp9RmZ0OAt6RGY9soqa_ieM/exec"

  export default {
    data: function() {
        return {
            hidePassword: true,
            input: {
                username: "",
                password: "",
                honeypot: ""
            }
        }
    },
    computed: {
      passwordType() {
        return this.hidePassword ? 'password' : 'text'
      }
    },        
    methods: {
      togglePass () {
        this.hidePassword = !this.hidePassword
      },                
        login: function () {
            var this1 = this;        
            this.$http.get(url+"?sheet=Users&action=retrieve")
                .then(function (response) {
                    if(this1.input.username != "" && this1.input.password != "" ) {  
                    for (let i = 0; i < response.data.length; i++) {
                    if(this1.input.username == response.data[i].user && this1.input.password == response.data[i].password && this1.input.honeypot == ""){
                        this1.$emit("authenticated", true);                
                        this1.$router.replace({ name: "blogpost" });
                    } 
                    }      
                    } else {
                       alert("A username and password must be present");
                    }
                })
                .catch(function (error) {
                     alert(error)
                })
        }
    }        

  }
</script>

<style>
.honeypot {
  display: none;
}    
</style>